  content: '';
  display: table;
  clear: both;
}

.float-grid--cell {
  float: left;
  box-sizing: border-box;
  text-align: center;
}

.float-grid--cell[class*="float-grid--push"], .float-grid--cell[class*="float-grid--pull"] {
  position: relative;
}

.float-grid--hide {
  display: none;
}

