.card {
  display: flex;
  flex-direction: column;
  position: relative;
  border-radius: 2px;
  overflow: hidden;
}

.card--image {
  display: flex;
  align-items: center;
  justify-content: stretch;
  padding: 16px;
}

.card--image-text {
  display: flex;
  align-self: flex-end;
  margin: 0;
  font-size: 24px;
  line-height: 38px;
}

.card--content {
  padding: 16px;
}

.card--title-text {
  margin: 0;
  font-size: 24px;
  line-height: 38px;
}

.card--sub-title-text {
  margin: 0;
  font-size: 14px;
  line-height: 28px;
  color: #9e9e9e;
}

.card--supporting-text {
  font-size: 14px;
  line-height: 20px;
}

.card--actions {
  padding: 8px;
  width: 100%;
}

.card--actions.card--border {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.card--actions > .button {
  padding: 0 8px;
}

.card--expand {
  flex-grow: 1;
}

.card--menu {
  position: absolute;
  right: 16px;
  top: 16px;
  color: #fff;
}

.tooltip {
  display: inline-block;
  padding: 0 8px;
  height: 22px;
  border-radius: 2px;
  background-color: #616161;
  color: white;
  font-size: 10px;
  line-height: 22px;
  opacity: .9;
}

.tooltip--large {
  height: 32px;
  font-size: 14px;
  line-height: 32px;
}

.list {
  display: block;
  padding: 8px 0;
  list-style: none;
}

.list--item {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  line-height: 1;
  display: flex;
  min-height: 48px;
  box-sizing: border-box;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  padding: 16px;
  cursor: default;
  overflow: hidden;
}

.list--item .list--item-primary-content {
  order: 0;
  flex-grow: 2;
  text-decoration: none;
  box-sizing: border-box;
  display: flex;
  align-items: center;
}

.list--item .list--item-primary-content .list--item-icon,
.list--item .list--item-primary-content .list--item-avatar {
  margin-right: 32px;
}

.list--item-icon {
  width: 24px;
  height: 24px;
  font-size: 24px;
}

.list--item-avatar {
  width: 40px;
  height: 40px;
  font-size: 40px;
  border-radius: 50%;
}

.textfield {
  position: relative;
  display: inline-block;
  margin: 0;
  padding: 20px 0;
  max-width: 100%;
  font-size: 16px;
}

.textfield--input {
  display: block;
  margin: 0;
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  padding: 8px 0;
  width: 100%;
  background: none;
  font-size: 16px;
  color: inherit;
  text-align: left;
}

.textfield.is-focused .textfield--input {
  outline: none;
}

.textfield.is-disabled .textfield--input {
  border-bottom: 1px dotted rgba(0, 0, 0, 0.26);
  background-color: transparent;
  color: rgba(0, 0, 0, 0.26);
}

.textfield--floating-icon .textfield--input {
  margin-left: 52px;
}

.textfield--label {
  position: absolute;
  top: 28px;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  width: 100%;
  font-size: 16px;
  color: #9e9e9e;
  overflow: hidden;
  white-space: nowrap;
  text-align: left;
  pointer-events: none;
